@import "../variables.scss";


.margin{
    position: relative;
    top: calc(#{-$desktopNavHeight} - 0.8em);
}
.cate{
    padding: 0 1rem;
}
ul.project{
    display: flex;
    flex-wrap: wrap;

    li{
        width: 100%;
        max-width: 30em;

        display: inline-flex;
        padding: 1em;
        border-radius: $borderRadius;

        box-shadow: 0 0 0 rgba(0, 0, 0, 0);
        background-color: $color_0;
        
        transition-property: background-color, box-shadow, color;
        transition-duration: $animTime;

        &:hover{
            box-shadow: 0 0 1em $color_6;
            background-color: $color_8;
            color: $color_6;
        }

        .img{
            flex-shrink: 0;
            width: 10em;
            height: 10em;
            margin-right: 1em;
            background-color: white;
            border-radius: $borderRadius;
            overflow: hidden;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .details{
            width: 100%;

            p{
                overflow: hidden;
                text-overflow: ellipsis;
            
                display: block;
                max-height: 7em;
                
                display: -webkit-box;
                -webkit-line-clamp:7;
                -webkit-box-orient: vertical;

                text-align: justify;
                // background-color: red;
            }
        }
    }
}