@import "../variables.scss";

.rating {
	padding: 1em;
	border-radius: $borderRadius;
	margin-bottom: 1em;

	color: $color_6;
	background-color: $color_1;
    box-shadow: 0 0 1em $shadowColor;
    
    li.rating{
    width: 100%;
    list-style-type: none;
    padding: 0 1em;
 
    .bar{
        width: 100%;
        height: 0.5em;
        display: flex;
        
        
        .block{
            width: 10%;
            height: 100%;
            background-color: $color_8;
            margin: 0 1px;
            text-align: center;
        }
    }
}
}
