@import "../variables.scss";

nav {
	display: flex;
	align-items: center;

	position: sticky;
	top: 0;
	z-index: 1;

	width: 100%;
	height: $desktopNavHeight;

	background-color: $color_1;
	color: $color_6;

	a {
		text-decoration: none;
		color: $color_6;
	}

	#logo {
		width: 3.5em;
		height: 3.5em;
		border-radius: 50%;

		background-image: url("/Images/site/favicon.jpg");
		background-size: cover;
		background-position: center;
		margin: 0 1em;
	}

	#mobileNav {
		position: fixed;
		left: -$mobileNavWidth;
		top: 0;

		width: $mobileNavWidth;
		height: 100vh;

		transition-duration: $animTime;

		#blocker {
			display: none;
			position: fixed;
			left: 0;
			top: 0;

			background-color: rgba(0, 0, 0, 0.637);

			width: 100vw;
			height: 100vh;
		}

		#panel {
			display: flex;
			flex-direction: column;

			position: absolute;
			left: 0;

			width: $mobileNavWidth;
			height: 100vh;

			background-color: $color_1;

			a {
				text-decoration: none;
				color: $color_6;
			}

			#navLinks {
				padding: 0 2em;
				margin: auto;
				display: block;
				width: 100%;
				li {
					width: 100%;
				}
			}
		}
	}

	#mobileMenuButton {
		position: relative;
		right: 0;

		width: 3em;
		height: 3em;
		background-image: url("/Images/site/bars.svg");
		background-repeat: no-repeat;
		background-position: center;
		background-size: 1em;

		margin-left: auto;
		margin-right: 2em;
		cursor: pointer;

		transition-duration: $animTime;

		@media screen and (min-width: $mobileWidth) {
			display: none;
		}
	}
}

main.home {
	nav {
		position: sticky;
	}
}

main.openMobileMenu {
	#mobileNav {
		left: 0;

		#blocker {
			display: block;
		}
	}

	#mobileMenuButton {
		background-image: url("/Images/site/close.svg") !important;
		// right: $mobileNavWidth !important;
	}
}
