@import "../variables.scss";

#navLinks {
	display: none;
	@media screen and (min-width: $mobileWidth) {
		display: flex;
	}

	margin-left: auto;
	margin-right: 1em;

	li {
        display: flex;
        flex-direction: column;
        justify-content: center;

        text-align: center;

		position: relative;
        
		width: 7em;
		height: $desktopNavHeight;
        
        border-bottom-style: solid;
		border-width: 0.3em;
		border-color: $color_0;
        
        cursor: pointer;

        transition-duration: $animTime;

		&.active {
			border-color: $color_7;
		}

		&:hover {
			border-color: $color_6;
			.lv2 {
				display: block;
			}
		}

		.lv2 {
			display: none;
			position: absolute;
			right: -1em;

			padding: 1em;
			background-color: $menuButtonHoverColor;

			top: 5em;
			bottom: initial;
			&.up {
				top: initial;
				bottom: 3.5em;
			}
			li {
				height: 3.5em;
			}
		}
	}
}
