@import "../variables.scss";

footer {
	$curveHeight: 5em;

	position: relative;
	overflow: hidden;
	padding-top: $curveHeight;
	&::before {
		position: absolute;
		top: -$curveHeight;
		right: 0;
		content: " ";
		display: block;
		background-color: $bodyColor;
		width: 200%;
		height: calc(#{$curveHeight} * 2);
		border-radius: 100%;
	}

	background-color: $color_1;
	width: 100%;

	div {
		padding: 1em;
		width: 100%;
		max-width: 30em;
		margin: auto;
		color: $color_6;
	}

	.name {
		display: inline-block;
		width: 35%;
	}
	.value {
		display: inline-block;
		width: 65%;
	}

	.social {
		display: flex;
		width: 100%;
		li {
			flex-shrink: 0;
			width: 3em;
			height: 3em;

			border-radius: 1.5em;
			border-top-right-radius: 0.2em;

			margin: 0.25em;
			background-color: $color_6;
			background-size: cover;
			background-position: center;
			border-width: 1px;
			border-style: solid;
			border-color: $color_6;

			box-shadow: 0 0 0 rgba(0, 0, 0, 0);

			transition-property: box-shadow;
			transition-duration: $animTime;

			&:hover {
				box-shadow: 0 0 1em $color_6;
			}
		}
		.facebook {
			background-image: url("/Images/site/facebook.svg");
		}
		.twitter {
			background-image: url("/Images/site/twitter.svg");
		}
		.whatsapp {
			background-image: url("/Images/site/whatsapp.svg");
		}
		.linkedin {
			background-image: url("/Images/site/linkedin.svg");
		}
		.github {
			background-image: url("/Images/site/github.svg");
		}
		.cv {
			width: 100%;
			border-radius: 1.5em;
			border-top-right-radius: $borderRadius;
			height: 3em;
			margin: 0.25em;

			text-align: center;
		}
		.space {
			width: 100%;
		}
	}
}
