@import "../variables.scss";

form{
    width: 100%;
    // padding: 2em 0;
    
    .inputGroup{
        position: relative;
        margin-bottom: 1em;
        
        input, textarea{
            width: 100%;
            padding: 0.5em;
            border-width: 0.1em;
            border-radius: $borderRadius;
            font-size: 1.2em;
            
            &.input-validation-error{
                border-color: $color_3;
            }
        }
        textarea{
            min-width: 100%;
            max-width: 100%;
            max-height: 8em;
            min-height: 8em;
            line-height: 1.2em;
        }
        
        &.required{
            &::before{
                content: " * ";
                color: $color_3;
            }
        }
        
        &.submit{
            display: flex;
            margin: 0;
            input{
                font-size: 1rem;
                padding: 1em;
            }
        }
        
        > span{
            color: $color_3;
        }
    }


    div.errorMsg{
        line-height: 2em;
        text-align: center;
        color: $color_3;
    }
}
